.video_box {
  position: relative;
  height: calc(100vh - 70px);
  background-color: #c1cff7;
  /*进行视频裁剪*/
  overflow: hidden;
  .video_bac {
    position: absolute;
    left: 50%;
    top: 50%;
    /*保证视频内容始终居中*/
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    /*保证视频充满屏幕*/
    object-fit: cover;
    min-height: 800px;
  }
}
