.content {
  width: 100%;
  height: calc(100vh - 70px);
  border-radius: 4px;
  background: url(../../static/image/bg.jpg) no-repeat center;
  background-size: cover;
  position: relative;

  .mask {
    width: 100%;
    height: 100%;
    padding: 20px;
    overflow: scroll;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: rgba(255, 255, 255, 0.5);
  }
}
