.header_top {
  width: 100vw;
  height: 70px;
  padding: 0 20px;
  .nav {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    overflow-x: scroll;
    .user {
      display: flex;
      align-items: center;
      cursor: pointer;
      img {
        height: 60px;
        width: 60px;
        border-radius: 50%;
      }
      span {
        margin-left: 10px;
        color: #222222;
        font-size: 18px;
        font-weight: 600;
      }
    }

    .search {
      width: 300px;
      border-radius: 10px;
    }
  }
}
